/**
 * Injectable completer that allows signaling completion of an asynchronous test. Used internally.
 */
export class AsyncTestCompleter {
private _resolve: (result: any) => void;
private _reject: (err: any) => void;
private _promise: Promise<any> = new Promise((res, rej) => {
    this._resolve = res;
    this._reject = rej;
  });
/**
 * @param {?=} value
 * @return {?}
 */
done(value?: any) { this._resolve(value); }
/**
 * @param {?=} error
 * @param {?=} stackTrace
 * @return {?}
 */
fail(error?: any, stackTrace?: string) { this._reject(error); }

  get promise(): Promise<any> { return this._promise; }

  static _tsickle_typeAnnotationsHelper() {
 /** @type {?} */
AsyncTestCompleter.prototype._resolve;
 /** @type {?} */
AsyncTestCompleter.prototype._reject;
 /** @type {?} */
AsyncTestCompleter.prototype._promise;
  }

}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
